import React from "react"
import {
  Box,
  Container,
  Typography,
  Accordion,
  AccordionSummary,
  AccordionDetails
} from "@mui/material"
import ExpandMoreIcon from "@mui/icons-material/ExpandMore"
// react.usememo
const faqs: { question: string; answer: string }[] = [
  {
    question: "What is this platform about?",
    answer: "This platform provides valuable insights and resources."
  },
  {
    question: "How can I sign up?",
    answer:
      'You can sign up by clicking on the "Sign Up" button on the top right corner.'
  },
  {
    question: "Is there a mobile app?",
    answer: "Yes! Our mobile app is available for both Android and iOS."
  }
]

const FAQSection: React.FC = () => {
  return (
    <Box sx={{ py: 8, bgcolor: "#f7f7f7" }}>
      <Container>
        <Typography
          variant="h4"
          sx={{
            textAlign: "center",
            mb: 6,
            color: "#011A27",
            fontWeight: "bold"
          }}
        >
          Frequently Asked Questions
        </Typography>
        <Box sx={{ maxWidth: "800px", mx: "auto" }}>
          {faqs.map((faq, index) => (
            <Accordion
              key={index}
              sx={{ mb: 2, borderRadius: "8px", boxShadow: 1 }}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon sx={{ color: "primary.main" }} />}
              >
                <Typography className="h6">{faq.question}</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>{faq.answer}</Typography>
              </AccordionDetails>
            </Accordion>
          ))}
        </Box>
      </Container>
    </Box>
  )
}

export default FAQSection
